<template>
    <div class="hometitle">
        <div class="header">
            <div class="icon-postion">
                <i class="fa fa-map-marker">
                    <span>广州</span>
                </i>
            </div>
            <div class="Home-name">
                <h3>驾校一点通</h3>
            </div>
            <div @click="togglemyactive" class="Home-my">
                <i class="fa fa-user"></i>
            </div>
        </div>
        <div class="tab">
            <ul>
                <li v-for="(item, index) in tabname" :key="index">
                    <span @click="goState(index)" :class="[item.active?'active':'']">{{item.name}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<style scope lang="less">
    .hometitle {
        padding: 1rem;
        .header {
            margin-bottom: 1rem;
            display: flex;

            .icon-postion {
                flex-basis: 100%;
                font-size: 2rem;

                span {
                    font-size: 1.5rem;
                    margin-left: 1rem;
                    color: rgb(163, 160, 160);
                }
            }

            .Home-name {
                flex-basis: 100%;
                font-size: 1.5rem;
                text-align: center;
            }

            .Home-my {
                font-size: 2rem;
                flex-basis: 100%;
                text-align: right;
            }
        }

        .tab {
            ul {
                display: flex;

                li {
                    text-align: center;
                    flex-basis: 100%;

                    span {
                        font-size: 1.4rem;
                        font-weight: bold;
                        &.active {
                            color: green;
                            font-size: 1.6rem;
                        }
                    }
                }
            }
        }
    }
</style>

<script>
    export default {
        name: "hometitle",
        methods: {
            goState(index) {
                this.tabname.forEach(element => {
                    if (element.active === true) {
                        element.active = false;
                        return
                    }
                });
                this.tabname[index].active = true
                this.$router.push(this.tabname[index].pushname)
            },
            togglemyactive(){
                this.$emit('togglemyactive')
            }
        },
        created(){
            this.tabname.forEach(element => {
                element.active=false
                if(element.pushname==this.$route.fullPath){
                    element.active=true
                }
            });
            console.log(this.$route.fullPath)
        },
        data() {
            return {
                tabname: [{
                        name: '科一',
                        active: true,
                        pushname: '/home/homesubjectone'
                    },
                    {
                        name: '科二',
                        active: false,
                        pushname: '/home/Homesubjecttwo/hometwocontent'
                    },
                    {
                        name: '科三',
                        active: false,
                        pushname: '/home/Homesubjectthree/homethreecontent'
                    },
                    {
                        name: '科四',
                        active: false,
                        pushname: '/home/Homesubjectfour'
                    }
                    // {
                    //     name: '拿本',
                    //     active: false,
                    //     pushname: '/home/Homesubjectbook'
                    // }
                ]
            }
        },
    }
</script>